export default {
	data() {
		return {
			id: 0,
			detail: {}
		}
	},
	methods: {
		async getNewsDetail() {
			const res = await this.$http({
				url: '/api/getnew/' + this.id
			})
			this.detail = res.data.message[0]
		}
	},
	onLoad(options) {
		this.id = options.id
		this.getNewsDetail()
	}
}
